<template>
  <v-card width="60vw" class="mx-auto mt-5">
    <v-list outlined class="pa-0 ma-0">
      <v-list-item-content class="pa-0 ma-0">
        <div class="d-flex red pa-3">
          <span class="ml-3 pt-2 white--text">POSTS</span>
          <v-spacer></v-spacer>
          <v-btn fab dark x-small color="primary" class="mr-5" @click="add">
            <v-icon dark>mdi-plus</v-icon>
          </v-btn>
        </div>
      </v-list-item-content>

      <div class="d-flex my-2">
        <v-list-item-subtitle class="ml-5">Title</v-list-item-subtitle>
        <v-list-item-subtitle>Description</v-list-item-subtitle>
        <v-list-item-subtitle>Created On</v-list-item-subtitle>
      </div>

      <v-list-item-group v-model="post" color="primary">
        <v-list-item dense v-for="post in posts" :key="post._id" @dblclick="show(post._id)">
          <v-list-item-content>
            <v-list-item-subtitle v-text="post.title"></v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-content>
            <v-list-item-subtitle v-text="post.description"></v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-content>
            <v-list-item-subtitle class="ml-10">
              {{
              post.createdOn | filteredDate
              }}
            </v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-icon class="ml-4" @click.stop="pick(post._id)">
            <v-icon small color="green">mdi-pencil</v-icon>
          </v-list-item-icon>
          <v-list-item-icon @click.stop="trash(post._id)">
            <v-icon small color="pink">mdi-trash-can</v-icon>
          </v-list-item-icon>
        </v-list-item>
      </v-list-item-group>
    </v-list>
    <!-- Show Dialog -->
    <v-dialog v-model="showDialog" max-width="350">
      <v-card>
        <v-card-title class="headline white--text success">Post Detail</v-card-title>
        <v-text-field outlined label="Title" class="px-5 pt-5" :value="title"></v-text-field>
        <v-textarea outlined label="Description" class="px-5" :value="description"></v-textarea>
        <v-text-field outlined label="Created on" class="px-5" :value="created | filteredDate"></v-text-field>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" text @click="showDialog = false">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- Add/Edit Dialog -->
    <v-dialog v-model="addDialog" persistent max-width="400">
      <v-card>
        <v-card-title v-if="mode == 'add'" class="headline white--text warning">Add Post</v-card-title>
        <v-card-title v-else class="headline white--text info">Edit Post</v-card-title>
        <v-form
          class="pa-5"
          ref="addform"
          v-model="valid"
          lazy-validation
          @submit.prevent="add('add')"
        >
          <v-text-field
            dense
            outlined
            label="Title"
            v-model="title"
            required
            :rules="rules"
            clearable
          ></v-text-field>

          <v-textarea
            dense
            outlined
            label="Description"
            v-model="description"
            :rules="rules"
            clearable
          ></v-textarea>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="info" text @click="addDialog = false">Cancel</v-btn>
            <v-btn color="success" text type="submit">Yes</v-btn>
          </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
    <!-- Delete Dialog -->
    <v-dialog v-model="dialog" max-width="290">
      <v-card>
        <v-card-title class="headline white--text red">Delete Post</v-card-title>
        <v-card-text class="mt-5">Confirm you want to delete this post ?</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="success" text @click="dialog = false">Cancel</v-btn>
          <v-btn color="warning" text @click="trash('trash')">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-card>
</template>

<script>
import PostService from "../postService.js";
import dayjs from "dayjs";

export default {
  name: "post",
  data() {
    return {
      valid: false,
      mode: "add",
      dialog: false,
      addDialog: false,
      showDialog: false,
      id: "",
      post: 0,
      posts: [],
      post1: {},
      title: "",
      description: "",
      created: "",
      rules: [
        v => !!v || "Name is required",
        v => (v && v.length > 3) || "Entry must be greater than 3 characters"
      ]
    };
  },
  async created() {
    this.posts = await PostService.getPosts();
  },
  methods: {
    add(a) {
      if (a == "add") {
        if (this.$refs.addform.validate()) {
          if (this.mode == "add") {
            this.insertPost();
          } else {
            this.updatePost();
          }
        } else return;
      }
      this.addDialog = true;
      this.resetAdd();
    },
    async insertPost() {
      await PostService.insertPost(this.title, this.description);
      this.posts = await PostService.getPosts();
      this.addDialog = false;
    },
    async updatePost() {
      await PostService.updatePost(this.id, this.title, this.description);
      this.posts = await PostService.getPosts();
      this.addDialog = false;
      this.id = "";
    },
    resetAdd() {
      this.mode = "add";
      this.title = "";
      this.description = "";
      this.$refs.addform.resetValidation();
    },

    pick(id) {
      this.id = id;
      this.mode = "edit";
      this.post1 = this.posts.find(x => x._id === id);
      this.title = this.post1.title;
      this.description = this.post1.description;
      this.addDialog = true;
    },

    trash(id) {
      if (id == "trash") {
        this.deletePost();
      } else {
        this.id = id;
        this.dialog = true;
      }
    },
    async deletePost() {
      await PostService.deletePost(this.id);
      this.posts = await PostService.getPosts();
      this.id = "";
      this.dialog = false;
    },
    async addPost() {
      await PostService.insertPost(this.title, this.description);
    },
    show(id) {
      this.post1 = this.posts.find(x => x._id === id);
      this.title = this.post1.title;
      this.description = this.post1.description;
      this.created = this.post1.createdOn;
      this.showDialog = true;
    }
  },
  filters: {
    filteredDate(d) {
      return dayjs(d).format("DD-MMM-'YY HH:mm");
    }
  }
};
</script>

<style></style>
